import {
  Grid,
  IconButton,
  MenuItem,
  TextField,
  Typography,
} from "@mui/material";
import axios from "axios";
import React, { useEffect, useState } from "react";
import EditIcon from "@mui/icons-material/Edit";
import { Link } from "react-router-dom";
function EditCategorie() {
  const [showTextField, setShowTextField] = useState(false);
  const [categorie, setCategories] = useState([]);
  useEffect(() => {
    axios
      .get(`http://localhost:8000/api/category/all`)
      .then((response) => {
        setCategories(response.data.categories);
        console.log("okey azo articles be");
        console.log(response);
      })
      .catch((error) => {
        console.error("tsy mandeha url articles");
        console.error(error);
      });
  }, []);
  const HandelShowTextField = () => {
    setShowTextField(true);
    //   navigate(`/update/category/${id}`);
  };

  return (
    <>
      <Grid
        container
        justifyContent={"center"}
        alignItems={"center"}
        height={"100vh"}
      >
        <Grid
          container
          justifyContent={"center"}
          alignItems={"center"}
          // minHeight={"100vh"}
          maxHeight={"98vh"}
          //   border={"red solid 2px"}
          direction={"column"}
          p={3}
        >
          <Typography p={3} variant="h4">
            Modifier Categorie
          </Typography>
          {categorie &&
            categorie.map((option) => (
              <Grid
                // border={"green solid 2px"}
                container
                justifyContent={"space-between"}
                //   textAlign={"justify"}
                alignItems={"center"}
                width={"60%"}
                height={"8vh"}
                position={"relative"}
              >
                <MenuItem
                  //   onClick={handlecloseAddNewCategory}
                  key={option.id}
                  value={option.id}
                  sx={{
                    justifyContent: "space-between",
                    // border: "solid red 2px",
                  }}
                >
                  {option.name.charAt(0).toUpperCase() + option.name.slice(1)}{" "}
                </MenuItem>
                {showTextField && (
                  <TextField
                    sx={{
                      my: 1,
                      position: "absolute",
                      bgcolor: "white",
                      width: "100%",
                      //   border: "red solid 2px",
                      zIndex: 555,
                    }}
                    //   fullWidth

                    id="outlined-multiline-static"
                    name="descri"
                    label="Description"
                    multiline
                    defaultValue={option.name}
                    InputLabelProps={{ shrink: true }}

                    //   rows={8}
                    //   value={formik.values.descri}
                    //   onClick={handlecloseAddNewCategory}
                    //   onChange={formik.handleChange}
                    //   onBlur={formik.handleBlur}
                    //   error={formik.touched.descri && Boolean(formik.errors.descri)}
                    //   helperText={formik.touched.descri && formik.errors.descri}
                  />
                )}
                <Grid>
                  <Link to={`${option.id}`}>
                    <IconButton onClick={HandelShowTextField}>
                      <EditIcon />
                    </IconButton>
                  </Link>
                </Grid>
              </Grid>
            ))}
        </Grid>
      </Grid>
    </>
  );
}

export default EditCategorie;
