/* eslint-disable jsx-a11y/img-redundant-alt */
import Footer from "./Footer";
import ContentCardFront from "./ContentCards";
import bannerImage from "../assets/image/typeMedia/BgImage/BGCity1.jpg";
import bannerImage2 from "../assets/image/CasqueBuilding.jpg";
import imgDesc1 from "../assets/image/typeMedia/BgImage/2206_w039_n003_272b_p1_272.jpg";
import imgDesc2 from "../assets/image/typeMedia/BgImage/Multitasking (1).gif";
import imgDesc3 from "../assets/image/typeMedia/BgImage/BGCity2.jpg";
import {
  Box,
  Button,
  Divider,
  Grid,
  Stack,
  Tabs,
  Typography,
} from "@mui/material";
import { useEffect, useRef, useState } from "react";
import axios from "axios";
import HeaderXS from "./Header/HeaderXS";
import HeaderXL from "./Header/HeaderXL";
import { useTheme } from "@mui/material/styles";
import useMediaQuery from "@mui/material/useMediaQuery";
import RobotoRegular from "../assets/fonts/Nunito-Italic-VariableFont_wght.ttf";
import BoltIcon from "@mui/icons-material/Bolt";

function Contenu() {
  // const [animatedText, setAnimatedText] = useState("");
  const [showFullText, setShowFullText] = useState(false);
  const [datas, setMyDatas] = useState([]);
  const theme = useTheme();
  const isXs = useMediaQuery(theme.breakpoints.only("xs"));
  const isSm = useMediaQuery(theme.breakpoints.only("sm"));
  const isMd = useMediaQuery(theme.breakpoints.only("md"));
  const isLg = useMediaQuery(theme.breakpoints.only("lg"));
  const parallaxRef = useRef(null);
  const parallaxRef2 = useRef(null);
  const parallaxRef3 = useRef(null);

  useEffect(() => {
    const parallaxEffect = () => {
      const scrolled = window.scrollY;
      const parallaxValue = scrolled * 0.7; // Ajustez cette valeur pour contrler l'effet de parallaxe
      parallaxRef.current.style.backgroundPositionY = `${parallaxValue}px`;
      parallaxRef2.current.style.backgroundPositionY = `${parallaxValue}px`; // Ajoutez les rfrences manquantes
      parallaxRef3.current.style.backgroundPositionY = `${parallaxValue}px`; // Ajoutez les rfrences manquantes
    };

    window.addEventListener("scroll", parallaxEffect);

    return () => {
      window.removeEventListener("scroll", parallaxEffect);
    };
  }, []);
  // const textToAnimate = `
  // Bienvenue  chez <br />
  // Lowe's <br />
  // `;

  // useEffect(() => {
  //   let index = 0;
  //   const intervalId = setInterval(() => {
  //     setAnimatedText((prevText) => {
  //       const newText = textToAnimate.substring(0, index);
  //       const spanTag = `<span style="font-size: 100px; color: #EBCC24; font-family: 'RobotoRegular', sans-serif;">Lowe's</span>`;
  //       return newText.replace(/Lowe's/, spanTag);
  //     });
  //     index++;

  //     if (index > textToAnimate.length) {
  //       clearInterval(intervalId);
  //     }
  //   }, 50);

  //   return () => clearInterval(intervalId);
  // }, []);
  useEffect(() => {
    axios
      .get("http://localhost:8000/api/articles/")
      .then((response) => {
        setMyDatas(response.data.articles);
        console.log("okey azo articles");
        console.log(response);
      })
      .catch((error) => {
        console.error("tsy mandeha url articles");
        console.error(error);
      });
  }, []);

  let maxWords;
  if (isXs) {
    maxWords = 30;
  } else if (isSm) {
    maxWords = 40;
  } else if (isMd) {
    maxWords = 50;
  } else if (isLg) {
    maxWords = 80;
  } else {
    maxWords = 40; // Valeur par dfaut pour les autres tailles d'cran
  }

  const text = `Imagine une plateforme de gestion de magasin innovante, conue pour
    simplifier et optimiser toutes les facettes de la gestion
    commerciale. Notre site offre une interface conviviale permettant de
    grer les stocks, suivre les ventes, et grer les employs en toute
    efficacit. Grce  des fonctionnalits avances, comme la gestion
    des commandes fournisseurs, la gnration de rapports en temps rel
    et la facilit d'intgration avec les systmes de paiement, notre
    solution amliore la productivit des commerces. Que ce soit pour
    les petites boutiques ou les grandes enseignes, notre site est
    l'outil complet pour maximiser l'efficacit et stimuler la
    croissance du commerce.`;

  const trimmedText = showFullText
    ? text
    : text.split(" ").slice(0, maxWords).join(" ");
  const showMoreText = !showFullText && text.split(" ").length > maxWords;

  const toggleShowMore = () => {
    setShowFullText(!showFullText);
  };
  const [largeurEcran, setLargeurEcran] = useState(window.innerWidth);

  useEffect(() => {
    const handleResize = () => {
      setLargeurEcran(window.innerWidth);
    };

    window.addEventListener("resize", handleResize);

    return () => {
      window.removeEventListener("resize", handleResize);
    };
  }, []);
  return (
    <>
      {/* XS (Extra Small) : <576px
SM (Small) : 576px
MD (Medium) : 768px
LG (Large) : 992px
XL (Extra Large) : 1200px */}

      <Box>
        {largeurEcran < 900.99 ? (
          <Box sx={{ display: "none" }}>
            <HeaderXS />
          </Box>
        ) : (
          <Box>
            <HeaderXL />
          </Box>
        )}
        {largeurEcran > 900.99 ? (
          <Box sx={{ display: "none" }}>
            <HeaderXL />
          </Box>
        ) : (
          <Box>
            <HeaderXS />
          </Box>
        )}
      </Box>

      <Grid
        container
        justifyContent={"center"}
        overflow={"hidden"}
        width={"100%"}
        // p={2}
      >
        <Box
          ref={parallaxRef}
          sx={{
            textAlign: "center",
            width: "100%",
            height: {
              xs: "72vh",
              md: "100vh",
            },
            backgroundImage: `url(${bannerImage})`,
            backgroundSize: "cover",
            boxShadow: " inset 0px 65px 30px -7px rgba(0,0,0,0.51);",
            overflow: "hidden",
          }}
        >
          {/* <Hidden smDown>
            <img
              src={bannerImage}
              alt="sary-fandrakofana"
              style={{ width: "100%", height: "100vh" }}
            />
          </Hidden> */}
          <Grid
            // border={"red solid 2px"}
            height={"100%"}
            container
            justifyContent={"center"}
            alignItems={"center"}
            sx={{
              backdropFilter: "blur(1px)", // Appliquer un flou de 5px  l'arrire-plan
              backgroundColor: "rgba(0, 0, 0, 0.5)", // Couleur de fond avec transparence pour l'effet de flou
            }}
          >
            <Stack direction={"column"} justifyContent={"center"}>
              <Typography
                sx={{
                  fontSize: {
                    xs: "7vw", // Hauteur pour xs
                    md: "3vw", // Hauteur pour md
                  },
                  textAlign: {
                    xs: "center", // Hauteur pour xs
                    md: "lef", // Hauteur pour md
                  },
                }}
                color={"white"}
                variant="h8"
                fontFamily={"monospace"}
                fontWeight={"bold"}
                fontStyle={"italic"}
              >
                Bienvenue chez <br />
                <span
                  style={{
                    fontFamily: "sans-serif",
                    fontStyle: "normal",
                    fontSize: "120px",
                    color: "#EBCC24",
                  }}
                >
                  Lowe's
                </span>
              </Typography>
              <Grid container justifyContent={"center"}>
                <Button
                  variant="contained"
                  style={{
                    // border: "2px solid #EBCC24",
                    justifyContent: "center",
                    width: "30%",
                    margin: "auto",
                    borderRadius: 8,
                    color: "#95c732",
                    backgroundColor: "white",
                  }}
                >
                  Dcouvre
                </Button>
              </Grid>
            </Stack>
          </Grid>
          {/* <Box
            height={"100vh"}
            sx={{ border: "red solid 3px" }}
            position={"absolute"}
            top={10}
            zIndex={58}
          ></Box> */}
        </Box>

        <Grid
          sx={{
            height: {
              xs: "100%",
              md: "80vh",
            },
            my: {
              xs: 5,
              sm: 3,
            },
          }}
          container
          direction={{
            xs: "column-reverse",
            sm: "column-reverse",
            md: "column-reverse",
            lg: "row",
          }}
          justifyContent={{
            xs: "center",
            sm: "center",
            md: "center",
            lg: "space-around",
          }}
          alignItems={{ xs: "center", md: "center", lg: "center" }}
        >
          <Grid
            // border={"green solid 2px"}
            // borderRadius={5}
            container
            textAlign={"center"}
            alignItems={"center"}
            justifyItems={"center"}
            sx={{
              width: {
                xs: "70%",
                sm: "50%",
                md: "40%",
              },
              height: {
                xs: "20%",
                md: "24vw",
              },
              borderRadius: {
                xs: 0,
                sm: 3,
              },
              overflow: "hidden",
            }}
          >
            <img
              src={imgDesc3}
              alt="photoDesc"
              style={{ borderRadius: 10, width: "100%" }}
            />
          </Grid>

          <Grid
            container
            item
            xs={4}
            sm={6}
            md={6}
            direction={"column"}
            justifyContent={"center"}
            // border={"green solid 2px"}
            // width={"60%"}
            sx={{
              width: {
                xs: "95%",
                sm: "80%",
                md: "50%",
              },
            }}
          >
            <Typography
              variant="h3"
              // color={"#95c732"}
              fontFamily={"monospace"}
              fontStyle={"italic"}
              textAlign={"center"}
            >
              Description
            </Typography>
            <Grid container justifyContent={"center"}>
              <Divider sx={{ width: "50%" }}>
                <BoltIcon />
              </Divider>
            </Grid>
            <Grid
              container
              justifyContent={"center"}
              alignItems={"center"}
              // border={"red solid 3px"}
              p={3}
            >
              <Typography
                variant="h6"
                fontFamily={RobotoRegular}
                textAlign={"left"}
                sx={{
                  width: {
                    xs: "100%",

                    md: "100%",
                  },
                }}
              >
                {trimmedText}
                {showMoreText && (
                  <span
                    onClick={toggleShowMore}
                    style={{ cursor: "pointer", color: "blue" }}
                  >
                    ...voir plus
                  </span>
                )}
                {showFullText && (
                  <span
                    onClick={toggleShowMore}
                    style={{ cursor: "pointer", color: "blue" }}
                  >
                    ...voir moins
                  </span>
                )}
              </Typography>
            </Grid>
          </Grid>
        </Grid>
        <Grid
          ref={parallaxRef3}
          container
          height={"70vh"}
          sx={{
            backgroundImage: `url(${bannerImage2})`,
            backgroundSize: "cover",
          }}
          // boxShadow={"0px 12px 5px 2px rgba(0,0,0,0.42)"}
        >
          <Grid
            sx={{
              backdropFilter: "blur(1px)", // Appliquer un flou de 5px  l'arrire-plan
              backgroundColor: "rgba(0, 0, 0, 0.5)", // Couleur de fond avec transparence pour l'effet de flou
            }}
            container
            width={"100%"}
            justifyContent={"center"}
            alignItems={"center"}
          >
            <Typography
              variant="h4"
              textAlign={"center"}
              fontFamily={"monospace"}
            >
              <span
                style={{
                  fontSize: "6vh",
                  fontStyle: "italic",
                  color: "#EBCC24",
                }}
              >
                Lowe's
              </span>
              <br />
              <span
                style={{ fontWeight: "bold", fontSize: "8vh", color: "white" }}
              >
                Excellence sans compromis.
              </span>
            </Typography>
          </Grid>
        </Grid>
        <Box
          display={"flex"}
          flexDirection={"column"}
          // boxShadow={"inset 0px 30px 30px -7px rgba(0,0,0,0.51)"}
          // paddingX={2}
          pt={3}
          // my={4}
          // mx={0.5}
          sx={{
            width: "100%",
          }}
          // border={"red solid 2px"}
        >
          <Box>
            <Grid
              container
              // border={"red solid 2px"}
              direction={{
                xs: "column-reverse",
                sm: "row",
                md: "row",
                lg: "row",
              }}
              justifyContent={"space-around"}
              alignItems={"center"}
            >
              <Grid
                // border={"green solid 2px"}
                borderRadius={5}
                container
                textAlign={"center"}
                alignItems={"center"}
                justifyItems={"center"}
                sx={{
                  width: {
                    xs: "100%",
                    md: "30vw",
                  },
                  height: {
                    xs: "100%",
                    md: "30vw",
                  },
                  overflow: "hidden",
                }}
                sm={5}
                md={4}
                lg={4}
              >
                <img src={imgDesc1} alt="photoDesc" style={{ width: "100%" }} />
              </Grid>
              <Grid
                padding={{ xs: 2, md: 3, lg: 3 }}
                margin={{ xs: 2, sm: "2vh 0", md: 1, lg: 1 }}
                alignItems={"center"}
                width={{ xs: "100", md: "100%", lg: "50%" }}
                sx={{ borderRadius: 5, bgcolor: "#EBCC24" }}
                item
                // xs={12}
                sm={6.5}
                md={7}
                lg={7}
              >
                <Typography
                  variant="h4"
                  fontFamily={"monospace"}
                  fontStyle={"italic"}
                  textAlign={"center"}
                  sx={{
                    fontSize: {
                      // xs: "1px",
                      sm: "3.5vh",
                      md: "5vh",
                      // lg: "7vh",
                    },
                  }}
                >
                  Explorez la Compltude <br />
                  chez Lowe's
                </Typography>
                <Typography
                  textAlign={"left"}
                  variant="h6"
                  fontFamily={RobotoRegular}
                  sx={{
                    textAlign: {
                      xs: "justify",
                      sm: "left",
                      md: "left",
                    },
                    fontSize: {
                      xs: "2.8vh",
                      sm: "2vh",
                      md: "2.7vh",
                      // lg: "3.5vh",
                    },
                  }}
                >
                  Chez Lowe's, nous sommes fiers de vous offrir une gamme
                  complte de produits et de services pour rpondre  tous vos
                  besoins. Que vous recherchiez des produits de haute qualit,
                  un service client exceptionnel ou une exprience d'achat
                  pratique, nous avons tout ce qu'il vous faut, et plus encore.
                </Typography>
              </Grid>
            </Grid>
          </Box>
          <Box pb={6}>
            <Grid
              container
              // border={"green solid 2px"}
              direction={{
                xs: "column",
                sm: "row",
                md: "row",
                lg: "row",
              }}
              justifyContent={"space-around"}
              alignItems={"center"}
            >
              <Grid
                padding={{ xs: 2, md: 3, lg: 3 }}
                margin={{ xs: 2, sm: "2vh 0", md: 1, lg: 1 }}
                alignItems={"center"}
                width={{ xs: "100", md: "100%", lg: "50%" }}
                sx={{ borderRadius: 5, bgcolor: "#95c732" }}
                item
                // xs={12}
                sm={6.5}
                md={7}
                lg={7}
              >
                <Typography
                  variant="h4"
                  fontFamily={"monospace"}
                  fontStyle={"italic"}
                  textAlign={"center"}
                  sx={{
                    fontSize: {
                      // xs: "1px",
                      sm: "3.5vh",
                      md: "5vh",
                      // lg: "6vh",
                    },
                  }}
                >
                  Lowe's est l pour vous, toujours  vos cts
                </Typography>
                <Typography
                  textAlign={"left"}
                  variant="h6"
                  fontFamily={RobotoRegular}
                  sx={{
                    textAlign: {
                      xs: "justify",
                      sm: "left",
                      md: "left",
                    },
                    fontSize: {
                      xs: "2.8vh",
                      sm: "2vh",
                      md: "2.7vh",
                      // lg: "2vh",
                    },
                  }}
                >
                  Bienvenue chez Lowe's - Votre Solution de Gestion de Magasin
                  Disponible  Tout Moment ! Chez Lowe's, nous comprenons que le
                  monde du commerce ne dort jamais. C'est pourquoi nous sommes
                  fiers de vous offrir une solution de gestion de magasin
                  disponible 24 heures sur 24, 7 jours sur 7. Que vous soyez un
                  propritaire de petite boutique ou une grande enseigne, nous
                  sommes l pour vous accompagner  chaque tape de votre
                  parcours commercial.
                </Typography>
              </Grid>
              <Grid
                // border={"green solid 2px"}
                borderRadius={5}
                container
                textAlign={"center"}
                alignItems={"center"}
                justifyItems={"center"}
                sx={{
                  width: {
                    xs: "100%",
                    md: "30vw",
                  },
                  height: {
                    xs: "100%",
                    md: "30vw",
                  },
                  overflow: "hidden",
                }}
                sm={5}
                md={4}
                lg={4}
              >
                <img src={imgDesc2} alt="photoDesc" style={{ width: "100%" }} />
              </Grid>
            </Grid>
          </Box>
        </Box>
        <Grid
          ref={parallaxRef2}
          container
          height={"70vh"}
          sx={{
            backgroundImage: `url(${bannerImage})`,
            backgroundSize: "cover",
          }}
          // boxShadow={"0px 12px 5px 2px rgba(0,0,0,0.42)"}
        >
          <Grid
            sx={{
              backdropFilter: "blur(1px)", // Appliquer un flou de 5px  l'arrire-plan
              backgroundColor: "rgba(0, 0, 0, 0.5)", // Couleur de fond avec transparence pour l'effet de flou
            }}
            container
            width={"100%"}
            justifyContent={"center"}
            alignItems={"center"}
          >
            <Typography
              variant="h4"
              textAlign={"center"}
              fontFamily={"monospace"}
            >
              <span
                style={{
                  fontSize: "6vh",
                  fontStyle: "italic",
                  color: "#EBCC24",
                }}
              >
                Lowe's
              </span>
              <br />
              <span
                style={{ fontWeight: "bold", fontSize: "8vh", color: "white" }}
              >
                Excellence sans compromis.
              </span>
            </Typography>
          </Grid>
        </Grid>
        <Box
          py={3}
          boxShadow={10}
          sx={{
            width: "100%",
            borderRadius: "8px",
            backgroundColor: "white",
          }}
        >
          <Typography
            sx={{
              textAlign: {
                xs: "center",
              },
            }}
            variant="h2"
            color={"#95c732"}
            paddingX={6}
          >
            Arrivage
          </Typography>
          <Stack direction="row" alignItems="center">
            <Box
              display={"flex"}
              margin="auto"
              flexDirection={"row"}
              width={"100%"}
              // border={"red solid 2px"}
              px={2}
              sx={{
                overflowX: "hidden",
                "&::-webkit-scrollbar": {
                  width: 0,
                },
              }}
            >
              <Tabs variant="scrollable">
                {datas &&
                  datas.map((e) => (
                    <Box key={e.id}>
                      <Box
                        sx={{
                          minWidth: { xs: 300, sm: 350 },
                          p: { xs: 1, sm: 2 },
                          borderRadius: 2,
                          overflow: "hidden",
                        }}
                      >
                        <ContentCardFront datas={e} />
                      </Box>
                    </Box>
                  ))}
              </Tabs>
            </Box>
          </Stack>
        </Box>
        {/* <CarouselPub/> */}
        <Footer />
      </Grid>
    </>
  );
}

export default Contenu;
