import React, { useState } from 'react';
import TextField from '@mui/material/TextField';
import Button from '@mui/material/Button';
import Select from '@mui/material/Select';
import MenuItem from '@mui/material/MenuItem';

function NameSelector() {
  const [selectedName, setSelectedName] = useState(""); // tat pour stocker le nom slectionn
  const [modifiedName, setModifiedName] = useState(""); // tat pour stocker le nom modifi
  const [isEditing, setIsEditing] = useState(false); // tat pour suivre si l'dition est en cours

  // Fonction pour grer la modification du nom slectionn
  const handleModify = () => {
    setIsEditing(true); // Active le mode dition
    setModifiedName(selectedName); // Initialise l'input avec le nom slectionn
  };

  // Fonction pour enregistrer le nom modifi
  const handleSave = () => {
    setIsEditing(false); // Dsactive le mode dition
    setSelectedName(modifiedName); // Met  jour le nom slectionn avec le nom modifi
  };

  return (
    <div>
      <Select
        value={selectedName}
        onChange={(e) => setSelectedName(e.target.value)}
        displayEmpty
      >
        <MenuItem value="" disabled>
          Slectionnez un nom
        </MenuItem>
        <MenuItem value="name1">Nom 1</MenuItem>
        <MenuItem value="name2">Nom 2</MenuItem>
        <MenuItem value="name3">Nom 3</MenuItem>
        {/* Ajoutez autant d'options que ncessaire */}
      </Select>
      {!isEditing ? (
        <Button variant="contained" onClick={handleModify}>Modifier</Button>
      ) : (
        <div>
          <TextField
            label="Nom modifi"
            value={modifiedName}
            onChange={(e) => setModifiedName(e.target.value)}
          />
          <Button variant="contained" onClick={handleSave}>Enregistrer</Button>
        </div>
      )}
    </div>
  );
}

export default NameSelector;
